import { ClickElementType, ClickElementTypes, ElementTypes, EventAction, EventActions } from '../../utility/constants';
import globalDataLayerService from './global-data-layer-service';
import analyticsManager from './analytics-manager';
import { removeLastRoutingSegment } from '../../entities/routing-key';
import { TDataLayer } from '@porsche/globaldatalayer-events/projects/pcom/all/types';

type DataLayerEventSenderType = {
  datalayerEventAction: EventAction;
  analyticsId: string;
  text: string;
  link: string;
  elementId: string;
  clickElementType: ClickElementType;
  customDatalayerProperties: TDataLayer;
};
export function sendDataLayerEvent({
  datalayerEventAction = EventActions.NAVIGATION_CLICK,
  analyticsId,
  text,
  link,
  elementId,
  clickElementType,
  customDatalayerProperties = {}
}: DataLayerEventSenderType): void {
  globalDataLayerService.pushDataLayerEvent(datalayerEventAction, {
    componentClick: {
      clickElementId: analyticsId,
      clickElementName: text,
      targetElement: analyticsManager.getModelTargetElement(elementId, text),
      clickElementType,
      ...(clickElementType === ClickElementTypes.Link ? { targetUrl: link } : {})
    },
    componentDisplay: {
      displayElementId: analyticsManager.calculateDisplayElementId(elementId),
      displayElementType: analyticsManager.calculateDisplayElementType(
        removeLastRoutingSegment(elementId),
        ElementTypes.menuItem
      ),
      displayElementName: text
    },
    ...customDatalayerProperties,
    ...(datalayerEventAction === EventActions.MODEL_RANGE_CLICK
      ? {
          car: {
            modelRangeName: text.toLowerCase()
          }
        }
      : {})
  });
}
